
import {helpQuery} from '@/api/help';
import {informationList} from '@/api/personal'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
name: 'personInfo',
components: {Group, Cell},
data() {
return {
informationList: [],
}
},
mounted () {
informationList({"hrPersonInformationClassId": this.$route.query.param}).then(response => {
if (response.data && response.data) {
this.informationList = response.data.rows.map(function (item, index) {
item.hrPersonalInformationList.map(function (it, index) {
it.flag = true;
return it;
});
return item;
});
} else {
this.informationList = [];
}
}).catch(error => {
});
},
created() {
},
methods: {
showDetailHistory: function (items) {
items.flag = !items.flag;
}
}
}



